import React from 'react'
import aboutImage from './assets/Car-rental-image.jpg'
import { NavLink } from 'react-router-dom'
import Contact from './Contact'

const Home = () => {
  return (
    <div className="container">
    <div className='row'>

        <div className='col-lg-6 p-5'>
            <h1 className='display-1'>Your Trusted Partner Since <span className='text-danger'>2010</span></h1>
            <p>Explore wide varieties of Cars with affordable prices</p>

            <NavLink to = '/about'>
            <button className='btn btn-outline-danger me-3'>Know More</button>
            </NavLink>

            <NavLink to = '/contact'>
            <button className='btn btn-outline-success'>Book Now</button>
            </NavLink>
            
        </div>
        <div className='col-lg-6 p-5'>
            <img src={aboutImage} className='img-fluid aboutImg' alt='car'></img>
        </div>
          
    </div>
    </div>
  )
}

export default Home
