import axios from "axios";
import React, { useState } from "react";

const AddCars = () => {
    /* Declaration of states*/ 
    const [name, setName] = useState("")
    const [pricePerDay, setPricePerDay] = useState("")
    const [capacity, setCapacity] = useState("")
    

    /*Create new car*/ 
    const submitHandler = (e)=> {
        e.preventDefault()
        axios.post(`http://localhost:4000/cars`,{name,pricePerDay,capacity})
        .then(()=> alert("New Car Added"))
        .catch((error)=>console.log(error))
    }

    return (
    <div className="container p-5">
       <div className="col-md-6 shadow mx-auto p-5">
            <h2 className="text-center">Add Cars</h2>
                <form onSubmit={submitHandler}> 
                    {/* onchange event is used to get data from inputs  */}
                    <input type="text" onChange={(e)=>setName(e.target.value)}  placeholder="Enter car name" className="form-control mb-3" />
                    <input type="text" onChange={(e)=>setPricePerDay(e.target.value)} placeholder="Enter per day price" className="form-control mb-3" />
                    <input type="text" onChange={(e)=>setCapacity(e.target.value)} placeholder="Enter seating capacity " className="form-control mb-3"/>
                    <input type="submit" className="form-control text-bg-success" />
                </form>
               
       </div>
    </div>
    )
}
export default AddCars