import axios from "axios";
import React, { useEffect, useState } from "react";
import DataTable from "datatables.net-dt";

const ViewCars = () => {
    const [data, setData] = useState([])
    const [name, setName] = useState("")
    const [pricePerDay, setPricePerDay] = useState("")
    const [capacity, setCapacity] = useState("")
    const [id, setId] = useState("")


    /* Below useEffect Fetches card data form api*/
    useEffect(() => {
        axios.get(`http://localhost:4000/cars`)
            .then((res) => setData(res.data))
            .catch((error) => console.log(error))

            setTimeout(() => {
                let table = new DataTable('#myTable')
            },5000)
    })

    /* delete car logic from api*/
    const deleteCar = (id) => {
        axios.delete(`http://localhost:4000/cars/${id}`)
        .then(()=>alert("Car deleted successfully.."))
        .catch((error)=>console.log(error))
    } 
    /* get details of car on edit button click*/ 
    const getCarDetails = (id) => {
        axios.get(`http://localhost:4000/cars/${id}`)
        .then((res)=>{
            setName(res.data.name)
            setPricePerDay(res.data.pricePerDay)
            setCapacity(res.data.capacity)
            setId(res.data.id)
            
        })
        .catch((error)=>console.log(error))
    }
    /* onSubmit is used to show updated data in the api for edit form of car*/ 

    const updateHandler = (e) => {
        e.preventDefault() //prevent page from reloading
        axios.put(`http://localhost:4000/cars/${id}`, {id,name,pricePerDay,capacity})
        .then(()=> alert("Car details updated successfully.."))
        .catch((error)=> console.log(error))
    }

    return (
        <div className="container p-5">
            <h2 className="text-center">View Cars</h2>
            <table className="table table-striped" id="myTable">
                <thead>
                    <tr>
                        <th>Car ID</th>
                        <th>Car Name</th>
                        <th>Per day price of car</th>
                        <th>Seating Capacity</th>
                        <th colSpan={2}>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        data.map((car, index) => {
                            return (
                                <tr key={index}>
                                    <td>{car.id}</td>
                                    <td>{car.name}</td>
                                    <td>{car.pricePerDay}</td>
                                    <td>{car.capacity}</td>
                                    <td>
                                        <button className='btn btn-primary' onClick={()=>getCarDetails(car.id)}
                                        data-bs-target = '#updatecar' data-bs-toggle = 'modal' >
                                        <i className='bi bi-pencil'></i>
                                        </button>
                                    </td>
                                    <td>
                                        <button className='btn btn-danger' onClick={()=> deleteCar(car.id)}>
                                        <i className='bi bi-trash'></i>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
            <div className="modal fade" data-bs-backdrop = 'static'  id="updatecar" >
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">Update Car</h5>
                                <button className="btn-close" data-bs-dismiss = 'modal'></button>
                            </div>
                            <div className="modal-body">
                            <form onSubmit={updateHandler}>
                                <input type="text" value = {name} onChange = {(e)=> setName(e.target.value)} className="form-control mb-3" />
                                <input type="text" value = {pricePerDay} onChange={(e)=> setPricePerDay(e.target.value)} className="form-control mb-3" />
                                <input type="text" value={capacity} onChange={(e)=> setCapacity(e.target.value)} className="form-control mb-3" />

                                <input type="submit" className="form-control text-bg-success" data-bs-dismiss = 'modal'/>
                            </form>
                            </div>    
                        </div>
                    </div>
            </div>
        </div>

    )
}
export default ViewCars