/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { NavLink } from "react-router-dom";

const About = () => {
    return (
        <section className="container-fluid p-0">
            <div className="text-bg-dark p-5 text-center">
                <h5>Welcome to RentalCar, your trusted partner for hassle-free and reliable car rental services.</h5>

                <nav aria-label="breadcrumb mx-auto ">
                    <ol className="breadcrumb justify-content-center gap-2" >
                       <NavLink to = '/'><li className="breadcrumb-item text-light"> Home </li></NavLink> 
                       <NavLink to = '/services'><li className="breadcrumb-item text-primary text-danger"><span className="text-white"> / </span> Services</li></NavLink>
                    </ol>
                </nav>

            </div>
            <div className="container p-5">
                <div className="row">
                    <div className="col-lg-6  p-md-5 p-3 d-flex">
                        <div className="shadow p-5 h-100 mission bg-dark bg-gradient text-light">
                            <h3>Our Mission</h3>
                            <p>To provide seamless, reliable, and affordable car rental solutions that cater to the diverse needs of our customers.</p>
                        </div>
                    </div>
                    <div className="col-lg-6 p-md-5 p-3 d-flex">
                    <div className="shadow p-5 h-100 vision text-light bg-dark bg-gradient">
                            <h3>Our Vision</h3>
                            <p>To become the most trusted and preferred car rental service, empowering people to travel with freedom, flexibility, and confidence.</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )

}
export default About;