import axios from 'axios';
import React, { useState } from 'react'


const AddServices = () => {
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    // const [serviceImg, setServiceImg] = useState("")

    const submitHandler = (e) => {
        e.preventDefault()
        axios.post(`http://localhost:4000/services`, {name, description})
        .then(()=> {
            alert("New services added");
            e.target.reset();
            /* If you use e.target.reset(), the form fields will be cleared automatically, 
            so you dont necessarily need to set the state to empty (setName(""), setDescription("").
            */
            // setName("");
            // setDescription("");
            
        }
        )
        .catch((error)=> console.log(error))
    }
    
  return (
    <div className='container p-5'>
      <div className='col-lg-6 shadow mx-auto p-5'>
        <h2 className='text-center'>Add Services</h2>
        <form onSubmit={submitHandler}>
            <input type='text'  onChange={(e)=> setName(e.target.value)} placeholder='Enter Service Name' className='form-control mb-3' />
            <textarea rows={5} cols = {30}  onChange={(e)=> setDescription(e.target.value)} className='form-control mb-3'placeholder='Enter Description'></textarea>
            <input type='submit' className='form-control text-bg-success'/>
        </form>
      </div>
    </div>
  )
}

export default AddServices
