import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { NavLink } from 'react-router-dom';

const Cars = () => {
  const [carDetails, setCarDetails] = useState([]);

  useEffect(() => {
    axios.get(`http://localhost:4000/cars`)
      .then((res) => setCarDetails(res.data))
      .catch((error) => console.log(error))
  })

  return (
    <div className='container p-5'>
      <h1 className='text-center'>Explore our wide range of our Cars</h1>
      <div className='row'>
        {
          carDetails.map((car, index) => {
            return (
              <div className='col-lg-4 mb-3' key={index}>
                <div className='card h-100'>
                  <div className='card-body d-flex flex-column justify-content-between'>
                    <h5 className='card-title'>{car.name}</h5>
                    <p>Price per day: {car.pricePerDay}/- Only</p>
                    <p>capacity: {car.capacity} Seater</p>
                    <img src={car.imgUrl} alt='car' className='img-fluid carImg'></img>
                    <div className='d-flex justify-content-between'>
                      <NavLink to = {`/carDetails/${car.id}`}>
                        <button className='btn btn-primary btn-sm'>Know More</button>
                      </NavLink>
                      <NavLink to={'/contact'}>
                        <button className='btn btn-success btn-sm'>Book Now</button>
                      </NavLink>
                      
                    </div>
                  </div>
                </div>
              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Cars
