import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Contact from './Contact'
import SendEnquiry from './SendEnquiry'


const CarDetails = () => {
    // directly accesing id by using useParams hook
    const {id} = useParams()
    const [car, setCar] = useState({}) //this is one of the object we want to read one car data at a time thats why we used object

    useEffect(() => {
        axios.get(`http://localhost:4000/cars/${id}`)
        .then((res)=>{
            setCar(res.data)
            console.log(res.data)
        })
        .catch((error)=>console.log(error))
    })
  return (
    <div className='container p-5'>
     <div className='row'>
        <div className='col-md-8'>
            <div className='card'>
            <div className='card-body text-center shadow mx-6 p-5'>
            <h2 className='card-title text-center'>{car.name}</h2>
            <img src={car.imgUrl} alt='car'/>
            <h2>{car.model}</h2>
            <h2>Seating capacity: {car.capacity} People</h2>
            <h2 >Price Per Day : {car.pricePerDay} Rs Only</h2>
            <h4>{car.description}</h4>
            
            </div>
            
            </div>     
        </div> 
        <div className='col-md-4'>
        <h5 className='mb-3 text-center text-success bg-white'>Book now to avail 40% Discount</h5>
            <SendEnquiry />
          
        </div>
     </div>
    </div>
  )
}

export default CarDetails
